* {
    padding: 0;
    margin: 0;
    font-size: 20px;
}

button {
    background-color: #333;
    color: #fff;
    border: 0.5px solid #000;
}

body {
    align-items: center;
    background-color: #eee;
    display: flex;
    justify-content: center;
    min-height: 100vh;
}

.calculator {
    align-content: center;
    align-items: stretch;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
}

#buttons {
    align-content: center;
    align-items: stretch;
    display: grid;
    grid-template-areas: 
        'clear clear div   mult'
        'seven eight nine  min'
        'four  five   six   plus'
        'one   two   three equals'
        'zero  zero  decim equals';
    grid-template-columns: repeat(4, 50px);
    grid-template-rows: repeat(5, 50px);
    justify-content: center;
}
#zero {
    grid-area: zero;
}
#one {
    grid-area: one;
}
#two {
    grid-area: two;
}
#three {
    grid-area: three;
}
#four {
    grid-area: four;
}
#five {
    grid-area: five;
}
#six {
    grid-area: six;
}
#seven {
    grid-area: seven;
}
#eight {
    grid-area: eight;
}
#nine {
    grid-area: nine;
}
#add {
    background-color: #ffc400;
    color: #000;
    grid-area: plus;
}
#subtract {
    background-color: #ffc400;
    color: #000;
    grid-area: min;
}
#multiply {
    background-color: #ffc400;
    color: #000;
    grid-area: mult;
}
#divide {
    background-color: #ffc400;
    color: #000;
    grid-area: div;
}
#clear {
    background-color: #d55;
    grid-area: clear;
}
#equals {
    background-color: #ff9900;
    color: #000;
    grid-area: equals;
}
#display {
    background-color: #444;
    color: #fff;
    padding: 15px;
    overflow: hidden;
    text-align: right;
}